import * as React from 'react';
import { useNavigate , useParams  } from "react-router-dom";

import { useContext, useState, useEffect } from 'react';

import noteContext from './NoteContext';


import API from "./API";


import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

import Snackbar from '@mui/material/Snackbar';


import Form from 'react-bootstrap/Form';

import DoneAllIcon from '@mui/icons-material/DoneAll';
export const EditStudent = () => {



    const [student, setstudent] = useState([]);



    const [fields, setfields] = useState([]);


    const [studentvalues, setStudentvalues] = useState([]);



    const data = useContext(noteContext);
    let token = data.token;
    const { sr } = useParams()



    const captureData = async (item, value) => {





        fields.forEach(element => {

            if (element.name == item) {



                element.value = value;

            }

        });

    }



    console.log(fields);



    const loadform = async () => {

        const res = await API.get("master/StudentFormFields", {
            headers: {
                'content-type': 'text/json', Authorization: 'Bearer ' + token
            }
        });


        setfields(res.data);



    }





    const handlesubmit = async () => {





        const res = await API.post("updateprofile", fields, {
            headers: {
                'content-type': 'text/json', Authorization: 'Bearer ' + token
            }
        });

        const dt = res.data;

        //setmessage(dt.message);


        //setOpen(true);


    }



    useEffect(() => {




        loadform();;


    }, []);



 


    return (

        <>
            {fields.map((item, index) => (

                <Form.Group key={index} className="mb-3" controlId="exampleForm.ControlInput1">
                    <Form.Label>{item.label}</Form.Label>
                    <Form.Control value={ item.value } onChange={(e) => captureData(item.name, e.target.value)} type="{item.type}"

                    />
                </Form.Group>

            ))}

            <Button onClick={handlesubmit} variant="contained"> <DoneAllIcon />    </Button>





        </>

    )
}